<template>
  <div class='transfer' style='display: block'>
    <h1>Transfer</h1>
    <div
      style='display: inline-block'
    >
      <p>From</p>
      <input
        id='from'
        style='width: 320px'
        v-model='from'
        type='text'
        placeholder='Link to Beet to use your address'
      >
      <p>To</p>
      <input
        id='to'
        style='width: 320px'
        v-model='to'
        type='text'
        placeholder='Insert recipient address'
      >
      <p>Amount (in float)</p>
      <input
        id='amount'
        style='width: 320px'
        v-model='amount'
        type='text'
        placeholder='Insert amount of BNB'
      >
    </div>
    <div
      style='display: inline-block; vertical-align: top'
    >
      <button
        class='transfer-button'
        type='submit'
        @click='send'
      >
        Push it
      </button>
    </div>
    <div style='margin-top: 10px'>
      <span>
        {{ message }}
      </span>
    </div>
  </div>
</template>

<script>
import '../lib/beet-js'
import BncClient from '@binance-chain/javascript-sdk'

export default {
  name: 'HelloWorld',
  data () {
    return {
      beet: window.beet,
      BncClient: BncClient,
      from: null,
      to: null,
      amount: null,
      bnbConnection: null,
      message: null
    }
  },
  mounted () {
    this.beet.get('Binance Widget Example', 'BNB_TEST').then(app => {
      this.app = app
      this.bnbConnection = app.BNB_TEST
      this.from = this.bnbConnection.getAccount().name
    }).catch(() => {
      this.from = 'Beet not found'
    })
  },
  methods: {
    send: async function () {
      try {
        if (this.from == null || this.to == null || this.amount == null) {
          this.message = 'Please fill in all fields'
          return
        }

        // initialize client
        const api = 'https://testnet-dex.binance.org/'
        let binanceClient = new this.BncClient(api)
        await binanceClient.initChain()

        // inject beet
        binanceClient = this.bnbConnection.inject(binanceClient)

        // use like usual
        let from = this.from
        let sequence = (await (await fetch(`${api}api/v1/account/${from}/sequence`)).json())
        sequence = (sequence.data ? sequence.data.sequence : 0) || 0

        let result = await binanceClient.transfer(
          from,
          this.to,
          parseFloat(this.amount),
          'BNB',
          'Binance Beet Widget Test',
          sequence
        )

        this.message = result
        console.log(this.message)
      } catch (err) {
        this.message = err
      }
    }
  }
}
</script>

<!-- Add 'scoped' attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal
}
ul {
  list-style-type: none;
  padding: 0
}
li {
  display: inline-block;
  margin: 0 10px
}
a {
  color: #42b983
}

.transfer-button {
  background-color: #c7088e;
  border: none;
  color: white;
  text-align: center;
  height: 150px;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  width: 150px;
  border-radius: 50%;
  margin-top: 40px;
  margin-left: 40px;
  cursor: pointer
}
.transfer-button:hover {
  background-color: #a40a7d
}
</style>



