<template>
  <div id="app">
    <div style="text-align: center;">
      <a style="text-decoration: none;" href="https://github.com/bitshares/beet"><h2>Beet - Your Blockchain Companion</h2></a>
      <p style="width: 400px; margin: auto;">
        In order to test this widget install Beet on your local computer, add an account from the BinanceChain Testnet
        and reload this page. Beet will ask if this app "Binance Widget Example" should be allowed to connect. After
        approving, your BinanceChain address should be correctly inserted in the "From" field. Fill out the rest and
        click "Push it", then you will see a popup in Beet asking for approval of the transfer.
      </p>
      <br />
      <p style="width: 400px; margin: auto;">
        This widget is closed source on purpose as it does not have to deal with private key security, and the user must
        approve any action. All private keys are safely stored within your locally running Beet and are NEVER exposed.
      </p>
    </div>
    <img width="200" src="./assets/logo.png">
    <HelloWorld/>
  </div>
</template>

<script>
import HelloWorld from './components/HelloWorld'

export default {
  name: 'BinanceBeetWidget',
  components: {
    HelloWorld
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



